.navbar {
  /* background-color: rgb(0, 255, 179); */
  background-color: #17b169;
  color: #fff;
}

.navbar {
  position: relative;
  z-index: 10;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px 20px;
  box-shadow: 0px 2px 5px rgb(0, 135, 94);
}

.navbar-list {
  display: flex;
  list-style-type: none;
  align-items: center;
  justify-content: space-between;
  font-weight: bold;
}

.navbar-list > li {
  margin: 0px 10px;
}
